* {
  font-family: Helvetica, Arial, sans-serif;
}

body{
  background-color: #68a0b4;
}

input{
  color:gray;
  font-size: 1.3rem;
  width: 100%;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  padding: 5px;
  text-align: justify;
}

.card img{
  max-height: 170px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
